#header-cont{
    position: relative;
    background: url('../../assets/bgIMG.png');
    background-repeat: repeat;
    background-size: cover;
    background-position: center;

    .app__wraper{
      padding: 0;
      
      .copyright{
        display: none;
      }
    }
}

.app__header{
    flex: 1;
    height: 100%;
    width: 100%;
    flex-direction: row;
    padding: 6rem 2rem 0;

    @media screen and (max-width: 1200px) {
        flex-direction: column;
    }

    @media screen and (max-width: 600px) {
        padding: 6rem 1rem 2rem;
    }
}

.app__header-info{
    flex: .65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;

    @media screen and (max-width: 2000px) {
        width: 100%;
        margin-right: 0rem;
    }
}

.app__header-badge{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    .badge-cmp, .tag-cmp{
        padding: 1rem 2rem;
        border: var(--white-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    }

    .tag-cmp{
       margin-top: 3rem;
       p{
        text-transform: uppercase;
        width: 100%;
       }
    }

    span{
        font-size: 2.5rem;
    }

    
    @media screen and (max-width: 1200px) {
        justify-content: flex-start;
        align-items: flex-start;
    }

}

.app__header-circles{
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
    margin-left: 1rem;

    div{
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background-color: var(--white-color);
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

        img{
            height: 60%;
            width: 60%;
        }
    }

    div:nth-child(1){
        height: 100px;
        width: 100px;
    }

    div:nth-child(2){
        margin: 1.75rem;
        height: 150px;
        width: 150px;
    }

    div:nth-child(3){
        height: 70px;
        width: 70px;
    }

     
    @media screen and (max-width: 1200px) {
        width: 100%;
        flex-direction: row;
        margin-left: 0;


        div{
            margin: 1rem;
        }
    }
}

.app__header-img{
    flex: 1;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;
    
    img{
        width: 100%;
        object-fit: contain;
        z-index: 1;
    }

    .overlay_circle{
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        height: 90%;
    }
}